// === VARIABLES ===

$primary-color: #ff63e8;
$secondary-color: #ffa6d7;

// === PREVIEW === //

.preview {
  &-block {
    position: relative;
    background-image: url("/img/school-prewiev-pro.jpeg");
    background-size: no-repeat;
    background-position: 35% 65%;
    background-size: cover;
    width: 100%;
    height: 650px;
    background-attachment: fixed;
    filter: blur(1.5px);
    @media (max-width: 900px) {
      background-position: 25% 75%;
    }
    @media (max-width: 650px) {
      background-position: 15% 85%;
    }
    &__title {
      color: #fff;
      position: absolute;
      top: 100px;
      left: 30px;
      max-width: 650px;
      & span {
        color: $secondary-color;
      }
      & p {
        font-size: 14px;
        width: 40%;

        @media (max-width: 650px) {
          width: 70%;
        }
      }
      & button {
        color: #fff;
      }
    }
  }
}
// === PREVIEW === //

.astro-links {
  &__subtitle {
    font-size: 16px;
    width: 100%;
    padding: 40px 80px;
    border: 1px solid $secondary-color;
    border-radius: 10px;
  }
  .btn-light {
    box-shadow: none;
    border: 1px solid $secondary-color;
  }
}

// === CONDITION === //

.condition {
  .tilesWrap {
    padding: 0;
    margin: 50px auto;
    list-style: none;
    text-align: center;

    li {
      display: inline-block;
      width: 75%;
      min-width: 200px;
      max-width: 320px;
      padding: 80px 20px 40px;
      position: relative;
      vertical-align: top;
      margin: 10px;
      font-family: "helvetica", san-serif;
      min-height: 45vh;
      background: #ffffff;
      border: 1px solid #252727;
      text-align: left;

      h2 {
        font-size: 60px;
        margin: 0;
        position: absolute;
        opacity: 0.2;
        top: 50px;
        right: 10px;
        transition: all 0.3s ease-in-out;
      }

      p {
        font-size: 16px;
        line-height: 18px;
        color: #000000;
        margin-top: 5px;
        width: 85%;
      }

      &:hover h2 {
        top: 0px;
        opacity: 0.6;
      }

      &:before {
        content: "";
        position: absolute;
        top: -2px;
        left: -2px;
        right: -2px;
        bottom: -2px;
        z-index: -1;
        background: #fff;
        transform: skew(2deg, 2deg);
      }

      &:after {
        content: "";
        position: absolute;
        width: 40%;
        height: 100%;
        left: 0;
        top: 0;
        background: rgba(255, 255, 255, 0.02);
      }

      &:nth-child(1):before {
        background: #c9ffbf;
        background: -webkit-linear-gradient(to right, #ffafbd, #c9ffbf);
        background: linear-gradient(to right, #ffafbd, #c9ffbf);
      }

      &:nth-child(2):before {
        background: #f2709c;
        background: -webkit-linear-gradient(to right, #ff9472, #f2709c);
        background: linear-gradient(to right, #ff9472, #f2709c);
      }

      &:nth-child(3):before {
        background: #c21500;
        background: -webkit-linear-gradient(to right, #ffc500, #c21500);
        background: linear-gradient(to right, #ffc500, #c21500);
      }

      &:nth-child(4):before {
        background: #fc354c;
        background: -webkit-linear-gradient(to right, #0abfbc, #fc354c);
        background: linear-gradient(to right, #0abfbc, #fc354c);
      }
    }
  }
}

